.post_content {
  line-height: 1rem;
  width: 80%;
  margin: auto;
  padding: 150px 0 100px 0;
}

.post_content img {
  padding-top: 50px;
  width: 100%;
}

.post_image {
  width: 100%;
}

#content br {
  display: none;
}
#content p br {
  display: inline;
}


@media only screen and (max-width: 728px) {
  .post_content {
    line-height: 1rem;
    width: 80%;
    margin: auto;
    padding: 125px 0 50px 0;
  }
}
