$backgroundColor: white;
$bodyColor: #202020;
$bodyFont: "basier_circleregular";


// * {
//   border-style: solid;
//   border-width: 1px;
// }

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  font-size: 0.9rem;

  margin: 0;
  padding: 0;
}

h1 {
  font-family: "Vollkorn", serif;
  font-style: italic;
  font-weight: 100;
  font-size: 3rem;

}


h2 {
  font-family: "basier_circleregular";
text-transform: uppercase;
font-size: .9rem;


}

p {
  font-family: 'Cormorant Garamond', serif;
  font-weight: 500;
  line-height: 2rem;
  font-size: 1.3rem;
}


a{
  color: #202020;
}

a:hover {
  text-decoration: none;
}